import React, { Component } from 'react';
import { Card, Button, Divider, Progress, Modal } from 'antd';
import {
    PlusCircleOutlined,
    CloseCircleOutlined,
    CheckCircleTwoTone,
    LoadingOutlined,
} from '@ant-design/icons';

class Device extends Component {
    componentDidUpdate = (prevProps) => {
        const { connectedDevice } = this.props;
        if (
            connectedDevice &&
            connectedDevice.GATTDisconnected &&
            prevProps.connectedDevice &&
            prevProps.connectedDevice.GATTDisconnected
        ) {
            console.log('componentDidUpdate');
            Modal.error({
                title: 'Failed to reconnect',
                content: 'Please try again later',
            });
        }
    };

    render() {
        const {
            device,
            connected,
            connectToDevice,
            removeDevice,
            batteryService,
            showDrawer,
            byteTransfered,
            server,
            GATTConnected,
            disconnectGattServer,
            connectedDevice,
            connectionRequest,
        } = this.props;

        let deviceState = '';
        if (GATTConnected) {
            deviceState = 'online';
        } else {
            if (connectionRequest) {
                deviceState = 'connecting...';
            } else {
                deviceState = 'offline';
            }
        }

        if (!connected) {
            return (
                <Card
                    title={
                        <>
                            <Divider type="vertical" />
                            <span>{device.name}</span>
                        </>
                    }
                    extra={
                        <div>
                            <Button
                                style={{ color: 'red' }}
                                shape="circle"
                                icon={
                                    <CloseCircleOutlined twoToneColor="#52c41a" />
                                }
                                onClick={() => removeDevice(device.uid)}
                            />
                        </div>
                    }
                    style={{ width: 300 }}
                >
                    <p>
                        <strong>Device: </strong>
                        {deviceState}
                    </p>
                    <p>
                        <strong>UID:</strong> {device.uid}
                    </p>
                    <Button
                        shape="circle"
                        icon={<PlusCircleOutlined />}
                        onClick={() => connectToDevice([{ name: device.name }])}
                    />
                </Card>
            );
        }

        return (
            <Card
                title={
                    <>
                        {GATTConnected && (
                            <CheckCircleTwoTone twoToneColor="#52c41a" />
                        )}
                        {connectionRequest && (
                            <LoadingOutlined twoToneColor="#52c41a" />
                        )}
                        <Divider type="vertical" />
                        <span>{device.name}</span>
                    </>
                }
                extra={
                    <div>
                        <Button
                            // type="primary"
                            style={{ color: 'red' }}
                            shape="circle"
                            icon={<CloseCircleOutlined />}
                            onClick={() => removeDevice(device.uid)}
                        />
                    </div>
                }
                style={{ width: 300 }}
            >
                <p>
                    <strong>Device: </strong>
                    {deviceState}
                </p>
                <p>
                    {GATTConnected && (
                        <button
                            onClick={() =>
                                disconnectGattServer(connectedDevice)
                            }
                        >
                            
                        </button>
                    )}
                </p>
                <p>
                    <strong>UID:</strong> {device.uid}
                </p>
                {batteryService && Number.isInteger(batteryService.level) && (
                    <p>
                        <strong>Battery level: </strong>
                        {batteryService.level}
                    </p>
                )}

                {server && (
                    <Button onClick={showDrawer}>Add recipe to device</Button>
                )}
                {byteTransfered ? (
                    <Progress
                        type="circle"
                        percent={Math.floor((byteTransfered * 100) / 9012)}
                        width={40}
                    />
                ) : (
                    ''
                )}
            </Card>
        );
    }
}

export default Device;
