import React, { Component } from 'react';
import { Row, Button, Tooltip, Divider } from 'antd';
import { PlusCircleOutlined } from '@ant-design/icons';
import { connect } from 'react-redux';
// import _ from 'lodash';
import * as actions from '../../../actions';
import {
    connectToDevice,
    disconnectGattServer,
} from '../../../actions/bluetooth';
import WebBluetooth from '../../WebBluetooth';
import Device from './Device';

const mapStateToProps = (state) => {
    const {
        auth: { token },
        data,
        data: { byteTransfered },
        bluetooth: {
            device,
            server,
            batteryService,
            devices: connectedDevices,
        },
    } = state;
    return {
        token,
        data,
        device,
        connectedDevices,
        server,
        batteryService,
        byteTransfered,
    };
};

const actionCreators = {
    recipe: actions.recipe,
    user: actions.user,
    connectToDevice,
    removeDevice: actions.removeDevice,
    disconnectGattServer,
};

class Devices extends Component {
    render() {
        const {
            connectToDevice,
            removeDevice,
            connectedDevices,
            showDrawer,
            server,
            batteryService,
            byteTransfered,
            disconnectGattServer,
        } = this.props;
        let devices =
            this.props.devices &&
            this.props.devices.map((device) => {
                return (
                    <Device
                        key={device.uid}
                        connected={
                            !!connectedDevices.find(
                                (connectedDevice) =>
                                    connectedDevice.name === device.name
                            )
                        }
                        GATTConnected={
                            !!connectedDevices.find(
                                (connectedDevice) =>
                                    connectedDevice.name === device.name &&
                                    connectedDevice.server &&
                                    connectedDevice.server.connected
                            )
                        }
                        device={device}
                        connectedDevice={connectedDevices.find(
                            (connectedDevice) =>
                                connectedDevice.name === device.name
                        )}
                        connectToDevice={connectToDevice}
                        removeDevice={removeDevice}
                        batteryService={batteryService}
                        showDrawer={showDrawer}
                        byteTransfered={byteTransfered}
                        server={server}
                        disconnectGattServer={disconnectGattServer}
                        connectionRequest={
                            !!connectedDevices.find(
                                (connectedDevice) =>
                                    connectedDevice.name === device.name &&
                                    connectedDevice.connectionRequest
                            )
                        }
                    />
                );
            });
        return (
            <>
                <Row gutter={16}>{devices || ''}</Row>
                <Divider />
                <Tooltip title="Add device">
                    <Button
                        shape="circle"
                        icon={<PlusCircleOutlined />}
                        onClick={() => connectToDevice()}
                    />
                </Tooltip>
                <WebBluetooth />
            </>
        );
    }
}

export default connect(mapStateToProps, actionCreators)(Devices);
